import { Link } from "react-router-dom";
function Navbar() {
  return (
    <div style={{ display: "flex", gap: "2rem", justifyContent: "center" }}>
      <Link to="/">Home </Link>
      <Link to="/about">About</Link>
      <Link to="/users">Users</Link>
    </div>
  );
}
export default Navbar;
